body {
  font-family: "Alatsi", sans-serif;
  margin: 0;
  overflow: hidden;
  background: black;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;

  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}

.title {
  font-size: 10vw;
  color: grey;
  z-index: 1; /* Asegúrate de que el título esté encima del canvas */
}

canvas {
  position: absolute; /* Asegúrate de que el canvas esté en el fondo */
  top: 0;
  left: 0;
  width: 100vw; /* Ocupa el ancho completo de la ventana */
  height: 100vh; /* Ocupa el alto completo de la ventana */
  pointer-events: none; /* Permitir clics a través del canvas */
}
